import React from 'react';
import ReactDOM from 'react-dom';
import { VscAdd } from 'react-icons/vsc'
import { VscChromeClose } from 'react-icons/vsc'
import {BsFillTrashFill} from 'react-icons/bs'
import './index.css'
class Todo extends React.Component{
    constructor(props){
        super(props);
        this.state = {text:[],show:false};
    
    }
  
   visible=()=>{
       var ss = document.getElementById("button").innerHTML;
       if(ss==="Add Task"){
        var s = document.getElementById("datafilled");
        if(s.value){
        var arr = this.state.text;
        arr.push(s.value);
        this.setState({text: arr});
        s.value="";
        document.getElementById("cross").style.visibility="hidden";
        }
       }
       else{
      var v = document.getElementById("sear");
    v.style.visibility="visible";
    v.style.maxHeight="700px";
  document.getElementById("button").innerHTML="Add Task";}
   }
   show = (event) => {
    var s = event.target.value;
    var ss = document.getElementById("cross");
    
   if(s){
      ss.style.visibility="visible";
   }
   else{
       ss.style.visibility="hidden";
   }

   }
   clr = () =>{
       var ss = document.getElementById("datafilled");
       ss.value="";
       document.getElementById("cross").style.visibility="hidden";
   }
   mouse=(inde)=>{
       var s = document.getElementById(inde);
       s.style.visibility="visible";
   }
   mouse1=(inde)=>{
       var s = document.getElementById(inde);
       s.style.visibility="hidden";
   }
   deletet =(id,event)=> {
     var s = this.state.text;
     for(var i =0;i<s.length;i++){
         if(s[i]===id){
            s.splice(i,1);
            break;
         }
     }
     this.setState({text:s});
  }

    render(){
        return(
            <div className="main">
                 <h2>TODO LIST</h2>
            <div className="search" id="sear">
                <input id="datafilled" type="text" placeholder="Type Here" onChange={this.show}></input><VscChromeClose id="cross" visibility="hidden" onClick={this.clr}/>
            </div>
           <div className="con" id="content" >
          {this.state.text.map(x=>(
              <div className="data" >
               {this.state.text.indexOf(x)}. {x} <BsFillTrashFill id={x.id} className="dustbin"  onClick={this.deletet.bind(this,x)}/>
              </div>
          ))}
           </div>
           <button onClick={this.visible} id="button"><VscAdd size="20px" fontSize="bold"/>    New Task</button>
            </div>
        );
    }
    componentDidUpdate(){
        var s = this.state.text;
        if(s.length)
        document.getElementById("content").style.display="block";
        else
        document.getElementById("content").style.display="none";
    }
}
ReactDOM.render(<Todo />,document.getElementById('root'));
